*,
*:before,
*:after {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.wrapper {
  display: flex;
  min-height: 100%;
  flex-direction: column;
}

.container {
  box-sizing: border-box;
  margin: 0 auto;
  width: 430px;
  padding: 0 20px;
}
@media (mxn-media("(min-width: ${desktop-width})")) {
  .container {
    width: 1440px;
    padding: 0 110px;
  }
}

@font-face {
  font-family: "Space Grotesk";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("https://mtrenkina.github.io/pozitivus-landing/fonts/SpaceGrotesk-Regular.woff2") format("woff2"), url("https://mtrenkina.github.io/pozitivus-landing/fonts/SpaceGrotesk-Regular.woff") format("woff");
}
@font-face {
  font-family: "Space Grotesk";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("https://mtrenkina.github.io/pozitivus-landing/fonts/SpaceGrotesk-SemiBold.woff2") format("woff2"), url("https://mtrenkina.github.io/pozitivus-landing/fonts/SpaceGrotesk-SemiBold.woff") format("woff");
}